
.Invisible {
    opacity: 0;
    position: absolute;
    top: -200px;
}
.HeaderBar { 
    position: absolute;
    top: 0;
    z-index: 1000;
    height: 60px;
    width: 100%;
    background-color: #276b27;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
    opacity: 0.9;
    
}

.HomeBackground {
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: #091d09;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    top: 0;
    bottom: 0;
    color: #eeeecc;
    font-size: 22pt;
    z-index: 1;
}

.DropdownMenuButtonParent {
    height: 100%;
    position: absolute;
    left: 0px;
    top: 0px;
    z-index: 2;
    margin: 0;
    padding: 0px;
    display: flex;
}

.DropdownMenuButtonParent p {
    flex: 1;
    align-self: center;
    padding-left: 10px;
    color: #ffeeee
}

.DropdownMenuButton {
    flex: 1;
    transition-duration: 0.25s;
    height: 100%;
    aspect-ratio: 1/1;
    border: 0px;
    background-color: #113311;
    color: #ffeeee;
    font-size: calc(10px + 2vmin);
    opacity: 0.75;
}

.DropdownMenuButton:hover {
    transition-duration: 0.25s;
    background-color: #082208;
    color: #dddddd;
}

.DropdownMenuButton:active {
    background-color: #001100;
}

.DropdownMenuContainerVisible {
    -webkit-animation: slideDown 0.5s;
    position: absolute;
    left: 0;
    top: 60px;
    background-color: #000000;
    opacity: 0.9;
    z-index: 1;
}

.DropdownMenuContainerInvisible {
    -webkit-animation: slideUp 0.5s;
    position: absolute;
    left: 0;
    top: 60px;
    background-color: #000000;
    opacity: 0;
    z-index: 1;
}

.DropdownMenuContainerVisible ul {
    list-style-type: none;
    margin: 0px;
    padding: 0px;
}

.DropdownMenuContainerVisible li {
    background-color: #276b27;
    opacity: 0.9;
    padding-top: 5px;
    padding-bottom: 5px;
    padding-left: 10px;
    padding-right: 10px;

}

.DropdownMenuContainerVisible a {
    text-decoration: none;
    color: #eeeecc;
}

.DropdownMenuContainerInvisible ul {
    list-style-type: none;
    margin: 0px;
    padding: 0px;
}

.DropdownMenuContainerInvisible li {
    background-color: #276b27;
    color: #eeeecc;
    opacity: 0.9;
    padding-bottom: 5px;
    padding-top: 5px;
    padding-left: 10px;
    padding-right: 10px;
}

.DropdownMenuContainerInvisible a {
    text-decoration: none;
    color: #eeeecc;
}

@keyframes slideDown {
    from {left: -500px;}
    to {left: 0px;}
}

@keyframes slideUp {
    from {opacity: 0.75; left: 0px;}
    to {opacity: 0; left: -500px;}
}

